body {
  font-size: 14px;
  line-height: 24px;
  width: 320px;
}
a {
  text-decoration: none;
}
.container {
  width: 280px;
  margin: 0 auto;
}
.main-nav {
  font-size: 18px;
  line-height: 32px;
  background-color: rgba(0, 0, 0, 0.3);
  color: #ffffff;
}
.page-header {
  background-color: #283645;
  color: #ffffff;
}
.page-header .page-header__wrapper {
  display: flex;
  justify-content: space-between;
}
.menu .menu__list {
  display: flex;
  flex-direction: column;
  list-style: none;
}
.menu .menu__list .menu__link {
  text-decoration: none;
  color: #ffffff;
}
